

const initParallax = () => {
  const items = document.querySelectorAll('.js-parallax');
  // console.log(items);

  if (items.length) {
    items.forEach((item) => {
      const layers = document.querySelectorAll('.js-parallax-layer');
      // console.log(layers);

      const onCardMove = (evt) => {
        const parallaxLeftOffset = item.getBoundingClientRect().left;
        const parallaxTopOffset = item.getBoundingClientRect().top;

        const coordX = evt.clientX - parallaxLeftOffset - 0.5 * item.offsetWidth;
        const coordY = evt.clientY - parallaxTopOffset - 0.5 * item.offsetHeight;

        layers.forEach((layer)=>{
          const layerSpeed = layer.dataset.speed;
          const x = (coordX * layerSpeed).toFixed(2);
          const y = (coordY * layerSpeed).toFixed(2);
          layer.setAttribute('style', `transform: translate(${x}px, ${y}px);`);
        });
      };

      const onCardOut = () => {
        item.removeAttribute('style');
      };

      item.addEventListener('mousemove', onCardMove);
      item.addEventListener('mouseout', onCardOut);
      // console.log(item);
    });
  }
};

export {initParallax};
