import {disablePageScroll, enablePageScroll} from '../../utils/scroll-lock';


const breakpoint = window.matchMedia('(min-width:1023px)');

const initMenu = () => {
  const header = document.querySelector('.header');
  const mainNav = document.querySelector('.main-nav');

  if (!mainNav) {
    return;
  }

  const toggleBtn = mainNav.querySelector('.main-nav__toggle');
  const isToggleElement = (element) => Boolean(element.closest('.main-nav__toggle'));
  const isScrollElement = (element) => Boolean(element.closest('.main-nav__scroll'));
  const isMenuClosed = () => mainNav.classList.contains('is-closed');
  const isMenuOpened = () => mainNav.classList.contains('is-opened');

  const closeMenu = () => {
    mainNav.classList.add('is-closed');

    setTimeout(()=> {
      mainNav.classList.remove('is-opened');
      header.classList.remove('header--bg');
      enablePageScroll(mainNav);
    }, 600);

    toggleBtn.blur();
  };

  const openMenu = () => {
    mainNav.classList.remove('is-closed');
    mainNav.classList.add('is-opened');
    header.classList.add('header--bg');
    disablePageScroll(mainNav);
  };

  const onEscKeyDown = (e) => {
    if (e.key === 'Esc' || e.key === 'Escape') {
      if (mainNav) {
        closeMenu();
      }
    }
  };

  const toggleMenu = () => {
    if (isMenuClosed()) {
      openMenu();
    } else {
      closeMenu();
    }
  };

  const onDocumentClick = (evt) => {
    const target = evt.target;
    if (!mainNav) {
      return;
    }

    if (isMenuOpened && !target.closest('.main-nav__wrapper') && target.closest('.main-nav') && !isToggleElement(target)) {
      document.removeEventListener('keydown', onEscKeyDown);
      closeMenu();
    }

    if (isToggleElement(target)) {
      toggleMenu();
    }

    if (isScrollElement(target)) {
      closeMenu();
    }

    if (isMenuOpened) {
      document.addEventListener('keydown', onEscKeyDown);
    }
  };

  const breakpointChecker = () => {
    if (breakpoint.matches) {
      enablePageScroll(mainNav);
      closeMenu();
    }
  };

  breakpoint.addListener(breakpointChecker);
  breakpointChecker();

  document.addEventListener('click', onDocumentClick);
};

export {initMenu};
