const videos = document.querySelectorAll('video');

let playing = true;
const playBtns = document.querySelectorAll('.block-video__play-button');

export const initVideos = () => {
  if (!videos) {
    return;
  }

  videos.forEach((videoEl) => {

    const hideCover = (parentElement) => {
      const btnElement = parentElement.querySelector('button');
      const bgElement = parentElement.querySelector('.block-video__bg');
      btnElement.classList.add('is-hidden');
      bgElement.classList.add('is-hidden');
    };

    const onPlayButtonClick = (evt) => {
      evt.preventDefault();
      const parentElement = evt.target.closest('.block-video');
      const videoWrapper = parentElement.querySelector('.block-video__wrapper');
      const video = videoWrapper.querySelector('video');
      const parentBlock = evt.target.closest('.js-video-parent');

      if (parentElement && video) {
        hideCover(parentElement);
        if (parentBlock) {
          const hidingElements = parentBlock.querySelectorAll('.js-hiding');
          hidingElements.forEach((el) => {
            el.classList.add('hide-element');
          });
        }
        video.play();
        video.setAttribute('controls', 'controls');

        // video.addEventListener('click', () => {
        //   if (video.paused) {
        //     video.play();
        //   } else {
        //     video.pause();
        //   }
        // });
      }
    };

    videoEl.addEventListener('play', function () {
      if (playing) {
        videos.forEach((el) => {
          el.pause();
        });
      }
      if (videoEl.paused) {
        playing = false;
        videoEl.play();
      } else {
        playing = true;
      }
    });

    playBtns.forEach((button) => button.addEventListener('click', onPlayButtonClick));
  });
};
