const moveTo = new window.MoveTo({
  duration: 800,
  easing: 'easeOutQuart',
});

const anchors = document.querySelectorAll('.js-trigger');

const initScrollToFooter = () => {
  if (anchors.length) {
    anchors.forEach((anchor) => {
      moveTo.registerTrigger(anchor);
    });
  }
};

export {initScrollToFooter};
